@use 'partials' as *;

*, *::before, *::after{
    box-sizing: border-box;
}

html{
    font-size: 10px;
}
body{
    min-height: 100vh;
    font-size: $size;
    font-family: $font2;
    font-weight: 700;
    margin: 0;
    color: $para;
}

p{
    font-family: $font1;
    font-weight: 400;
}

.container{
       display: flex;
       justify-content: center;
       align-items: center;
      min-height: 100vh; 
      background: red;

    .card{
        display: flex;
        // justify-content: center;
        flex-flow: row wrap;
        width: 86rem;
        margin: 2rem;
        // height: calc(100vh - 20rem);
      
       .card-1, .card-2, .card-3{
           flex-basis: 33.333%;
           padding: 4.5rem;

           h1{
               color: $headings;
           }

           p{
               line-height: 1.5;
           }
       }

        .card-1{
            background: $orange;
            border-radius: 1rem 0 0 1rem;

            .button{
                color: $orange;
            }
        }
        .card-2{
            background: $light-green;

            .button{
                color: $light-green;
            }
        }
        .card-3{
            background: $dark-green;
            border-radius: 0 1rem 1rem 0;

            .button{
                color: $dark-green;
            }
        }

        .button{
            display: inline-block;
            background: $headings;
            border-radius: 2.4rem;
            padding: 1rem 0;
            text-align: center;
            width: 70%;
            margin-top: 70%;
        }
    }
}


@media screen and (max-width: 670px) {

    .container .card{
        flex-direction: column;
        width: 70%;
        margin: 10%;

        .card-1{
            border-radius: 1rem 1rem 0 0;
        }
        .card-3{
            border-radius: 0 0 1rem 1rem;
        }

        .button{
            margin-top: 2rem;
        }
    }
    
}
@media screen and (min-width: 680px) and (max-width: 700px) {
    

   .container .card{
        // flex-direction: column;
        // width: 70%;
        margin: 2rem;


        .button{
            margin-top: 10%;
        }
    }

    .m1{width: 8.333%}
    .m2{width: 16.666%}
    .m3{width: 24.999%}
    .m4{width: 33.333%}
    .m5{width: 41.666%}
    .m6{width: 49.999%}
    .m7{width: 58.333%}
    .m8{width: 66.666%}
    .m9{width: 74.999%}
    .m10{width: 83.333%}
    .m11{width: 91.666%}
    .m12{width: 99.999%}
}